<template>
  <div class="login-wrap flex-center">
    <form class="form-horizontal">
      <img src="../../assets/logo.png">
      <h2>XX麻将代理系统</h2>
      <input class="form-control" placeholder="请输入用户id">
      <input type="password" class="form-control" id="inputPassword3" placeholder="请输入密码">
      <div class="login">
        <label class="login">
          <input type="checkbox"> 记住我
        </label>
        <router-link class="btn btn-default" to="/findpass">忘记密码</router-link>
      </div>
      <div class="login">
        <button class="btn btn-primary btn-lg btn-block" @click="gohome">登录</button>
      </div>
    </form>
  </div>
</template>

<script>
   export default {
     data () {
       return {}
     },
     methods: {
       gohome () {
         this.$router.push('/main')
       }
     }
   }
</script>

<style lang="scss" scoped>
  .login-wrap {
    height: 100%;
    .form-horizontal {
      padding: 15px;
      text-align: center;
      max-width: 400px;
      width: 100%;
      h2 {
        text-align: center;
      }
      .login {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @extend .form-control;
      }
      .form-control {
        ~ .form-control {
          margin-top: 10px;
        }
      }
    }
  }
</style>



