import Vue from 'vue'
import Router from 'vue-router'
// 登录页
import login from '@/components/page/login'
import findpass from '@/components/page/findpass'
import main from '@/components/page/main'

import mindex from '@/components/common/index'
import sell from '@/components/common/sell'
import recharge from '@/components/common/recharge'
import cRecord from '@/components/common/cRecord'
import sRecord from '@/components/common/sRecord'
import user from '@/components/common/user'
import club from '@/components/common/club'
import member from '@/components/common/member'
import spread from '@/components/common/spread'
import group from '@/components/common/group'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    // 登录页
    {
      path: '/login',
      component: login
    },
    // 忘记密码
    {
      path: '/findpass',
      component: findpass
    },
    // 主页面
    {
      path: '/main',
      component: main,
      children: [
        { path: '/main', component: mindex },
        { path: '/main/sell', component: sell },
        { path: '/main/recharge', component: recharge },
        { path: '/main/cRecord', component: cRecord },
        { path: '/main/sRecord', component: sRecord },
        { path: '/main/user', component: user },
        { path: '/main/club', component: club },
        { path: '/main/member', component: member },
        { path: '/main/spread', component: spread },
        { path: '/main/group', component: group }
      ]
    }
  ]
})



